@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
*,*::before,*::after{margin:0;padding:0}html{box-sizing:border-box}body{box-sizing:inherit;line-height:1.6;font-family:'Source Sans Pro', Arial, Helvetica, sans-serif;overscroll-behavior-y:none}.App{position:fixed;top:0;left:0;height:100%;width:100%;overflow:hidden;pointer-events:none}.container{display:grid;grid-template-rows:75vh 200vh repeat(2, 100vh)}.intro{grid-row:1 / 2;grid-column:1 / -1;position:relative;background:#171417;color:#eee;display:grid}.intro .intro-center{display:grid;grid-template-columns:repeat(4, -webkit-max-content);grid-template-columns:repeat(4, max-content);grid-gap:20px;font-size:75px;font-weight:300;align-self:baseline;justify-content:center;grid-template-rows:repeat(3, 1fr)}.intro .intro-center span:nth-child(1){grid-row:2 / 3;justify-self:end}.intro .intro-center span.top{grid-row:1 / 2;justify-self:center;grid-column:2 / 3;align-self:end}.intro .intro-center span,.intro .intro-center div.text{position:relative;grid-row:2 / 3}.intro .intro-center span span,.intro .intro-center div.text span{position:absolute;top:37px;font-size:16px}.intro .intro-center figure{grid-row:2 / 3;width:300px;height:150px}.intro .intro-bottom{align-self:baseline;display:flex;justify-content:space-between;margin:0 20px;text-transform:uppercase;font-size:13px}.nav{display:flex;align-items:center;justify-content:space-between;margin:0 20px;align-self:baseline}.nav__logo{font-size:30px;font-weight:bold}.navigation{display:flex;list-style:none;margin:15px 0}.navigation__item{padding:0 20px}.navigation__link{text-decoration:none;color:currentColor;text-transform:uppercase;font-size:14px}.section--one,.section--two{display:grid;grid-template-columns:repeat(8, 1fr);grid-template-rows:repeat(8, 1fr)}.section--one{grid-row:2 / 3}.section--one figure.img-bg{grid-row:1 / -1;grid-column:1 / -1}.section--one .wrapper-img{grid-row:5 / -1;grid-column:4 / -1;align-self:baseline;position:relative;height:500px}.section--one .img-reveal{width:350px;height:500px;-webkit-filter:contrast(0.6);filter:contrast(0.6)}.section--two{grid-row:3 / 4}.section--two h1{transform:translateY(200px);visibility:hidden;opacity:0;grid-row:1 / 3;grid-column:1 / -1;padding:50px 100px;font-weight:300;font-size:38px;transition:all 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955)}.section--two h1.reveal{visibility:visible;opacity:1;transform:translateY(0)}.section--two figure{height:130px;width:400px;grid-column:1 / -1;justify-self:center;grid-row:3 / -1;align-self:end;margin-bottom:40px;transition:all 0.7s cubic-bezier(0.455, 0.03, 0.515, 0.955)}.section--third{grid-row:4 / -1;background-color:#171417;color:#eee;display:grid}.section--third h1{align-self:end;font-weight:400;line-height:1.8;margin-bottom:30px;padding:30px 160px;visibility:hidden;opacity:0;transition:all 0.7s cubic-bezier(0.455, 0.03, 0.515, 0.955)}img{width:100%;height:100%;object-fit:cover;display:block}

